.note-container {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.notes-list {
  border: none;
  width: 370px;
  height: 100%;
  align-self: stretch;
}

/* .notes-list ion-item.selected {
  --background: #eee;
  color: #000;
} */

@media (prefers-color-scheme: dark) {

  .notes-list {
    background-color: #181818;
    border-color: #333;
  }

  .notes-list ion-item {
    --background: #1a1a1a;
  }

  .notes-list ion-item.selected {
    --background: #222;
  }

  .notes-list ion-item.selected ion-label {
    color: #fff !important;
  }

  ion-item {
    color: #777;
  }
}



.notes {
  max-height: 100%;
  overflow-y: auto;
  padding-top: 1px;
}

.notes .button.new-note {
  border: none;
  text-align: center;
}

.notes .button {
  color: var(--ion-color-medium);
  padding: 11px 21px;
  border-top: solid 1px var(--ion-color-light);
  cursor: pointer;
}

.notes .button:last-child {
  border-bottom: solid 1px var(--ion-color-light);
}

.notes .button:hover {
  background-color: var(--ion-color-light-tint);
}

.notes .button.selected {
  background-color: var(--ion-color-light-tint);
}
