#loginForm {
  margin: 0 auto;
  padding: 3em;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

#loginForm ion-input {
  width: 300px;
}

ion-button {
  width: fit-content;
}

ion-button[type="submit"] {
  margin-top: 3em;
  margin-bottom: 1em;
  width: 150px;
}