@font-face {
  font-family: 'Atkinson Hyperlegible';
  src: local('Atkinson Hyperlegible Bold Italic'), url(./fonts/Atkinson-Hyperlegible-BoldItalic-102.woff) format('woff');
  font-weight: bold;
  font-style: italic, oblique;
}
@font-face {
  font-family: 'Atkinson Hyperlegible';
  src: local('Atkinson Hyperlegible Italic'), url(./fonts/Atkinson-Hyperlegible-Italic-102.woff) format('woff');
  font-style: italic, oblique;
}
@font-face {
  font-family: 'Atkinson Hyperlegible';
  src: local('Atkinson Hyperlegible Bold'), url(./fonts/Atkinson-Hyperlegible-Bold-102.woff) format('woff');
  font-weight: bold;
}
@font-face {
  font-family: 'Atkinson Hyperlegible';
  src: local('Atkinson Hyperlegible Regular'), url(./fonts/Atkinson-Hyperlegible-Regular-102.woff) format('woff');
  font-style: normal;
}

body {
  scrollbar-width: thin;

  --ion-font-family: "Atkinson Hyperlegible", roboto, Helvetica, Arial, Sans-Serif;
}

@media (prefers-color-scheme: dark) {
