.ProseMirror {
  /* min-width: 700px; */
  padding: 30px 110px;

  font-family: "Atkinson Hyperlegible", sans-serif;
  line-height: 1.5em;
  font-size: 18px;
  color: #555;
}

.ProseMirror .hashtag {
  background-color: #555;
  color: #fff;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 0.8em;
}

.ProseMirror .heading {
  font-weight: bold;
}

.ProseMirror .heading::before {
  font-size: 16px;
  color: #888;
  content: "h" attr(level) " ";
  margin-left: -23px;
}

.ProseMirror .hashtag::before {
  /* content: '#'; */
}

#editor {
  border-radius: 0;
  min-height: 100%;
  border: none;
  width: 800px;
  overflow-y: auto;
}

@media (prefers-color-scheme: dark) {
  /* TODO: Exact to variables */
  .ProseMirror {
    color: #fff;
    background-color: #222;
  }

  #editor {
    background-color: #2a2a2a;
  }

  .ProseMirror-menubar {
    background-color: #2a2a2a;
    color: #777;
    border-color: #555;
    border: none;
  }
  .ProseMirror-menuseparator {
    border-color: #333;
  }
}

@import "prosemirrorEditor.css";